<template>
    <div>
        <van-tabs v-model="tabIndex" @change="_tab_change">
            <van-tab v-for="(v,k) in tabList" :key="k" :title="v">
                
                <ul style="background-color: #fff;border-top: 1px solid #eee;">
                    <li class="flex-between flex-vertical" style="border-bottom:1px solid #eee;">
                        <span class="otc-item-field flex-center">类型</span> 
                        <span class="otc-item-field flex-center">金额</span> 
                        <span class="otc-item-field flex-center">时间</span>
                        <span class="otc-item-field flex-center">状态</span>  
                    </li>
                    <li
                        v-for="(order,orderIndex) in orderList"
                        :key="orderIndex"
                        class="flex-between flex-vertical"
                        style="border-bottom:1px solid #eee;color:#666;"
                        @click="goDetail(order.otcCode)"
                    >
                        <span class="otc-item-field flex-center"><div style="color: #00FF00;display: block;clear:both;">{{order.otcTypeStr}} </div> <br /> <div style="color: #FF0000;display: block;clear:both;">【{{order.otcSaleorBuyStr}}】</div></span>
                        <span class="otc-item-field flex-center">{{order.otcPrice}}</span>
                        <span class="otc-item-field flex-center">{{order.createTime}}</span>  
                        <span class="otc-item-field flex-center">{{order.stateStr}}</span>   
                    </li>
                </ul>
                <p v-if="orderList.length === 0" class="flex-center" style="font-size:12px;color:#999;height:30px;">没有数据</p>

            </van-tab>
        </van-tabs>
    </div>
</template>

<script>
import { getOTCOrderList } from '@/api';
export default {
    data: ()=>({
        tabList: [
            '红包(挂单)',
            '红包(接单)',
            '通证(挂单)',
            '通证(接单)'
        ],
        tabIndex: 0,
        orderList: []
    }),
    computed: {
        otcType: function(){
            if(this.tabIndex === 0 || this.tabIndex === 1) return 1;
            else if(this.tabIndex === 2 || this.tabIndex === 3) return 2;
        },
        otcSaleorBuy: function(){
            if(this.tabIndex === 0 || this.tabIndex === 2) return 1;
            else if(this.tabIndex === 1 || this.tabIndex === 3) return 2;
        }
    },
    mounted(){
        this.getOTCOrderList();
    },
    methods: {
        getOTCOrderList(){
            let { otcType, otcSaleorBuy } = this;
            getOTCOrderList({
                otcType,
                otcSaleorBuy
            }).then(res=>{
                if(res.code === 0){
                    this.orderList = res.data.list.map(v=>{
                        let stateStr = '';
                        switch(v.otcStatus){
                            case '0':
                                stateStr = '挂单';
                                break;
                            case '1':
                                stateStr = '接单';
                                break;
                            case '2':
                                stateStr = '已付款待转账';
                                break;
                            case '3':
                                stateStr = '已转账';
                                break;
                            case '4':
                                stateStr = '已完成';
                                break;
                            case '7':
                                stateStr = '已取消';
                                break;
                            case '8':
                                stateStr = '已取消';
                                break;
                            case '9':
                                stateStr = '已取消';
                                break;
                            default: 
                                stateStr = '';
                                break;
                        }
                        v.stateStr = stateStr;

                        let otcTypeStr = '';
                        switch(v.otcType)
                        {
                            case '1':
                                otcTypeStr = '红包';
                                break;
                            case '2':
                                otcTypeStr = '通证';
                                break;
                            default:
                                otcTypeStr = '红包';
                                break;
                        }
                        v.otcTypeStr = otcTypeStr;

                        let otcSaleorBuyStr = '';
                        switch(v.otcSaleorBuy)
                        {
                            case '1':
                                otcSaleorBuyStr = '卖';
                                break;
                            case '2':
                                otcSaleorBuyStr = '买';
                                break;
                            default:
                                otcSaleorBuyStr = '卖';
                                break;
                        }
                        v.otcSaleorBuyStr = otcSaleorBuyStr;

                        return v;
                    });
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        _tab_change(){
            this.getOTCOrderList();
        },
         goDetail(id) {
            this.$router.push({
            name: "otcOrderdetail",
            query: { id: id }
      });
    },
    }
}
</script>

<style scoped>
    .otc-item-field{
        width: 25%;
        height: 50px;
        font-size: 14px;
        text-align: center;
    } 
</style>